/****************************************************************************************/
/* C9 Music Client */
/****************************************************************************************/
:root {
  font-family: "helvetica neue", helvetica, sans-serif;
  --default-font: "helvetica neue", helvetica, sans-serif;
}

/*
Use the following with the build tools to generate new client styles for fonts
Remove inherit font settings on the $font-family variables
@import url("https://use.typekit.net/att2ajc.css");

:root {
    font-family: futura-pt,
        "helvetica",
        times,
        serif;
}
*/
a:hover {
  color: #4CAAB1;
}

p {
  font-size: 1.6em;
}

.entry-content p.has-huge-font-size,
.entry-content p.has-large-font-size,
.entry-content p.has-larger-font-size,
.entry-content p.has-medium-font-size,
.entry-content p.has-normal-font-size,
.entry-content p.has-regular-font-size,
.entry-content p.has-small-font-size {
  line-height: 1.6;
}

/****************************************************************************************/
/* C9 Music Nav header */
/****************************************************************************************/
.header-navbar {
  min-height: 45px;
}

.header-navbar .navbar {
  background-color: transparent;
}

.header-navbar .navbar-toggler,
.header-navbar .nav-search .btn-nav-search,
.header-navbar .nav-order .nav-shop-link {
  color: #ffffff;
}

.header-navbar .navbar-toggler:hover,
.header-navbar .nav-search .btn-nav-search:hover,
.header-navbar .nav-search .btn-nav-search:hover .fa-search,
.header-navbar .navbar.navbar-light .nav-link:hover .fa,
.header-navbar .navbar-toggler:focus,
.header-navbar .nav-search .btn-nav-search:focus {
  color: #d2d2d2;
}

.header-navbar .navbar-small,
.blog .header-navbar,
.archive .header-navbar,
.single-post .header-navbar,
.error404 .header-navbar,
.search .header-navbar {
  background-color: #4CAAB1;
}

.navbar-small .header-navbar.jumpfix {
  min-height: 45px;
}

.header-navbar .justify-content-center {
  justify-content: flex-end !important;
}

.header-navbar .nav .search .nav-link .fa-search {
  font-size: 14px;
}

.dropdown-toggle::after {
  border-right: none;
  border-left: none;
}

.wp-block-button__link.is-style-outline {
  color: inherit;
}

.header-navbar .navbar .nav .nav-item,
.header-navbar .navbar .nav .nav-item .dropdown-item,
.header-navbar .navbar .nav .nav-item .nav-link,
.header-navbar .navbar .nav .search,
.header-navbar .navbar .nav a::after {
  line-height: 1;
  text-shadow: 0.5px 0.866px 4px rgba(0, 0, 0, 0.92);
}

.header-navbar .navbar .nav .nav-item {
  margin: 0px 14px;
}

.header-navbar .navbar .nav .nav-item .dropdown-menu .nav-item {
  margin: 0px 6px;
}

.header-navbar .navbar .nav .search .nav-search a {
  font-size: 14px;
}

.header-navbar .navbar .nav .nav-item .dropdown-item,
.header-navbar .navbar .nav .nav-item .nav-link {
  color: #ffffff;
  font-size: 14px;
  font-family: inherit;
  text-transform: uppercase;
  position: relative;
  letter-spacing: 1px;
  padding: 14px 0px;
}

.header-navbar .nav-order a,
.header-navbar .navbar-small .nav-order a {
  text-decoration: none;
}

.header-navbar .nav-order a .count,
.header-navbar .navbar-small .nav-order a .count,
.header-navbar .navbar .nav .nav-item .nav-link .count {
  font-size: 10px;
  margin-left: 2px;
}

.header-navbar .nav-item .nav-highlight {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 100%;
  bottom: 0;
  background: #ffffff;
  height: 4px;
  transition: right 0.3s;
  transition-timing-function: ease-out;
}

.header-navbar .nav-item > .nav-link:hover > .nav-highlight,
.header-navbar .nav-item > .nav-link:focus > .nav-highlight,
.header-navbar .nav-item:focus > .nav-link > .nav-highlight {
  right: 0% !important;
}

.header-navbar .navbar .nav .nav-item .dropdown-item {
  padding: 14px;
}

.header-navbar .navbar .nav .nav-item .dropdown-menu .nav-item .nav-link {
  padding: 14px 8px;
}

.header-navbar .nav .nav-item .dropdown-menu {
  background-color: #4CAAB1;
  border-radius: 0px;
  margin-top: 1em;
  border: 0px;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out;
}

.header-navbar .nav .nav-item .dropdown-menu.show {
  display: block;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out;
}

.header-navbar .nav .nav-item .dropdown-menu .dropdown-item:hover {
  background-color: #000000;
  color: #ffffff;
}

.nav-search,
.nav-toggle,
.nav-order {
  display: inline-block;
  line-height: 2;
}

.header-navbar .nav-order .nav-shop-link {
  font-size: 2em;
  margin: 0;
  line-height: 1;
  cursor: pointer;
}

.navbar-toggler[aria-expanded=true] .fa-bars:before {
  content: "\f00d";
}

.nav-search {
  margin-right: 10px;
}

@media only screen and (max-width: 991px) {
  .header-navbar .nav .nav-item.nav-woocommerce {
    display: none;
  }
}
@media only screen and (min-width: 992px) {
  .navbar-small-buttons .nav-order {
    display: none;
  }
}
.header-navbar .navbar .nav .nav-item.has-c9-icon {
  margin: 0px;
}

.header-navbar .navbar .nav .nav-item.has-c9-icon .nav-link {
  padding: 14px 13px;
  margin: 0px 2px;
}

.has-c9-icon .nav-link-text,
.has-c9-icon .nav-link .nav-highlight {
  display: none;
}

@media only screen and (max-width: 991px) {
  .has-c9-icon .nav-link-text {
    display: inline-block;
    margin-left: 6px;
  }
}
.has-c9-icon .nav-link:before {
  font-family: "fontawesome";
  font-size: 14px;
  font-weight: 400;
  transition: color 0.4s ease-in-out;
}

.c9-icon-email .nav-link:before {
  content: "\f003";
}

.c9-icon-spotify .nav-link:before {
  content: "\f1bc";
}

.c9-icon-youtube .nav-link:before {
  content: "\f16a";
}

.c9-icon-facebook .nav-link:before {
  content: "\f09a";
}

.c9-icon-instagram .nav-link:before {
  content: "\f16d";
}

.c9-icon-soundcloud .nav-link:before {
  content: "\f1be";
}

@-webkit-keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
@keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
/****************************************************************************************/
/* nav link hover*/
/****************************************************************************************/
.header-navbar .navbar .nav .nav-item .dropdown-item:hover,
.header-navbar .navbar .nav .nav-item .nav-link:hover,
.header-navbar .navbar:not(.navbar-small) .nav-link:hover,
.header-navbar .dropdown-item:hover,
.header-navbar .navbar-toggler:hover,
.header-navbar .nav-link:hover,
.header-navbar .nav > .nav-item > .nav-link:focus,
.header-navbar .nav-search .btn-nav-search:focus,
.header-navbar .nav-search .btn-nav-search:hover,
.header-navbar .nav-woocommerce .nav-link:hover,
.header-navbar .nav-woocommerce .nav-link:focus,
.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover,
.header-navbar .navbar:not(.navbar-small) .nav .nav-item.active .nav-link:hover,
.header-navbar .navbar:not(.navbar-small) .nav .nav-item .nav-link:hover,
.header-navbar .navbar .nav > .nav-item .nav-link:hover,
.header-navbar .navbar.navbar-small .nav .nav-item.active > .nav-link:hover,
.header-navbar .nav-search .btn-nav-search:focus,
.header-navbar .nav-search .btn-nav-search:hover,
.header-navbar .nav-search .btn-nav-search:hover .fa-search,
.header-navbar .navbar-toggler:focus,
.header-navbar .navbar-toggler:hover,
.header-navbar .navbar.navbar-light .nav-link:hover .fa,
.has-c9-icon .nav-link:hover:before,
.has-c9-icon .nav-link:focus:before {
  color: #ec971f;
}

.header-navbar .navbar .nav .nav-item > .nav-link:focus,
.header-navbar .navbar .nav .nav-item > .nav-link:active {
  color: #ffffff;
}

.header-navbar .navbar.navbar-small .nav .nav-item.active > .nav-link {
  color: #ffffff;
}

/****************************************************************************************/
/* mobile navigation background*/
/****************************************************************************************/
@media only screen and (max-width: 667px) {
  .header-navbar .navbar {
    background-color: #4CAAB1;
  }
}
@media only screen and (max-width: 991px) {
  .header-navbar .navbar-collapse,
.header-navbar .navbar-collapse.show {
    background-color: #4CAAB1;
    left: 0;
  }
}
/****************************************************************************************/
/* transparent page header navigation on desktop */
/****************************************************************************************/
@media only screen and (min-width: 768px) {
  #page-wrapper {
    margin-top: -115px;
  }
}
