}

#fullscreensearch form .btn {
  font-family: inherit;
  padding: 16px 55px;
  font-size: 1.2em;
  margin-top: 20px;
}

#fullscreensearch input[type=search] {
  border: 1px solid #eceeef;
  box-shadow: 0.5px 0.866px 4px rgba(61, 61, 61, 0.603);
}

.entry-content p a:not(.wp-block-button__link):not(.nav-tabs):not([class|=c9-share]):not(.c9-smooth):not(.nav-link),
