}

.i-tag:before,
.tags-links a:before {
  display: inline;
  content: "\f02b";
  font: normal normal normal 1.2em/1 FontAwesome;
  color: #4CAAB1;
  padding-right: 2px;
}

/****************************************************************************************/
/* index, posts, blog index */
/****************************************************************************************/
.c9-blog-releases {
  display: -ms-flex;
  display: flex;
  position: relative;
  overflow: visible;
  margin-top: 1.5em;
}

.c9-blog-releases .c9-blog-header {
  flex: 0 0 100%;
  max-width: 100%;
  display: -ms-flex;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (min-width: 415px) {
  .c9-blog-releases .c9-blog-header {
    flex: 0 0 50%;
    max-width: 50%;
    min-height: calc(70vh - 108px);
    margin-right: 2em;
    margin-bottom: 0;
  }
}
.c9-blog-info {
  border: 1px solid #eceeef;
  box-shadow: 0.5px 0.866px 4px rgba(61, 61, 61, 0.1);
  background-color: #ffffff;
}

.c9-blog-info h1 {
  font-size: 4em;
  line-height: 0.9;
}

.c9-blog-releases .c9-blog-posts {
  background-color: #ffffff;
  overflow-y: scroll;
  max-height: calc(100vh - 108px);
  -ms-scroll-snap-type: y proximity;
  scroll-snap-type: y proximity;
}

.c9-blog-releases .c9-blog-posts .entry-article .entry-image {
  min-width: 100%;
  height: auto;
  background-color: #ccc;
  border: 0.5px solid #eceeef;
}

.c9-blog-releases .c9-blog-posts .entry-article .post {
  width: 100%;
}

.c9-blog-releases .c9-blog-posts .entry-article {
  display: -ms-flex;
  display: flex;
  align-items: center;
  justify-content: center;
  scroll-snap-align: start;
}

.c9-blog-posts .entry-footer {
  margin-bottom: 30px;
}

@media only screen and (max-width: 414px) {
  .c9-blog-releases {
    display: block;
  }

  .c9-blog-releases .c9-blog-posts {
    overflow-y: visible;
    max-height: none;
  }

  .c9-blog-releases .c9-blog-header {
    min-height: 50vh;
  }

  .c9-blog-posts .entry-footer {
    padding: 0px;
  }
}
.c9-blog-releases .c9-blog-posts figure {
  position: relative;
  overflow: hidden;
}

.c9-blog-releases .c9-blog-posts figure::before {
  position: absolute;
  top: 0;
  left: -75%;
  z-index: 2;
  display: block;
  content: "";
  width: 50%;
  height: 100%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
  transform: skewX(-25deg);
}

.c9-blog-releases .c9-blog-posts figure:hover::before {
  -webkit-animation: shine 0.75s;
  animation: shine 0.75s;
}

@-webkit-keyframes shine {
  100% {
    left: 125%;
  }
}
@keyframes shine {
  100% {
    left: 125%;
  }
}
.c9-block-post-grid header .c9-block-post-grid-title {
  margin-bottom: 0px;
}

.c9-block-post-grid .c9-block-post-grid-excerpt,
.c9-block-post-grid .c9-block-post-grid-byline {
  display: none;
}

.c9-block-post-grid .c9-block-post-grid-text {
  display: grid;
  align-items: center;
  justify-content: center;
}

.entry-meta {
  color: #121212;
}

